"use client";

import SearchBox from "@/components/SearchBox";
import AnimeList from "@/components/AnimeList";
import { useAnimeList } from "@/hooks/useAnimeList";
import { useEffect } from "react";

export default function Page() {
  const { items, search, sortItems, loadMore, finished } = useAnimeList();

  // Carrega uma primeira página de animes aleatórios ao montar o componente
  useEffect(() => {
    loadMore("");
  }, []);

  return (
    <div
      style={{
        width: "100vw",
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 20,
      }}
    >
      <h1 style={{ color: "white", marginBottom: 20 }}>Buscar Animes</h1>

      <div style={{ maxWidth: 600, margin: "0 auto" }}>
        <SearchBox onSearch={search} />

        <div style={{ marginBottom: 20 }}>
          <button onClick={() => sortItems("nome")} style={{ marginRight: 10 }}>
            Ordenar por Nome
          </button>

          <button onClick={() => sortItems("ano")}>Ordenar por Ano</button>
        </div>

        <AnimeList items={items} loadMore={loadMore} finished={finished} />
      </div>
    </div>
  );
}
